import { evntBus } from "../../bus";
import Customer from "./Customer.vue";
export default {
  // props: ["pos_profile"],
  data() {
    return {
      pos_profile: "",
      pos_opening_shift: "",
      invoice_doc: "",
      customer: "",
      customer_info: "",
      items_discounts: 0,
      additional_discount: 0,
      total_tax: 0,
      total: 0,
      items: [],
      itemsPerPage: 1000,
      expanded: [],
      singleExpand: false,
      items_headers: [
        {
          text: "Name",
          align: "start",
          sortable: true,
          value: "item_name",
        },
        { text: "QTY", value: "qty", align: "center" },
        { text: "UOM", value: "stock_uom", align: "center" },
        { text: "Rate", value: "rate", align: "center" },
        { text: "Amount", value: "amount", align: "center" },
      ],
    };
  },
  components: {
    Customer,
  },
  computed: {
    total_qty() {
      this.close_payments();
      let qty = 0;
      this.items.forEach((item) => {
        qty += item.qty;
      });
      return flt(qty);
    },
    subtotal() {
      this.close_payments();
      let sum = 0;
      this.items.forEach((item) => {
        sum += item.qty * item.rate;
      });
      return flt(sum);
    },
  },
  methods: {
    remove_item(item) {
      const index = this.items.findIndex((el) => el === item);
      this.items.splice(index, 1);
      const idx = this.expanded.findIndex((el) => el === item);
      if (idx >= 0) {
        this.expanded.splice(idx, 1);
      }
    },
    add_one(item) {
      item.qty++;
      // this.$forceUpdate();
    },
    subtract_one(item) {
      item.qty--;
      if (item.qty <= 0) {
        this.remove_item(item);
      }
      // this.$forceUpdate();
    },
    add_item(item) {
      const index = this.items.findIndex(
        (el) => el.item_code === item.item_code
      );
      if (index === -1) {
        const new_item = { ...item };
        new_item.qty = 1;
        new_item.discount_amount = 0;
        new_item.discount_percentage = 0;

        new_item.price_list_rate = item.rate;
        this.update_items_details([new_item]);
        this.update_item_detail(new_item);
        this.items.unshift(new_item);
      } else {
        // this.update_items_details([this.items[index]]);
        this.items[index].qty++;
        this.items[index].actual_qty = item.actual_qty;
      }
    },
    cancel_invoice() {
      const doc = this.get_invoice_doc();
      if (doc.name) {
        frappe.call({
          method: "posawesome.posawesome.api.posapp.delete_invoice",
          args: { invoice: doc.name },
          async: true,
          callback: function (r) {
            if (r.message) {
              frappe.show_alert(
                // TODO : replace whith proper alert
                {
                  message: __(`${r.message}`),
                  indicator: "green",
                },
                5
              );
            }
          },
        });
      }
      this.items = [];
      this.customer = this.pos_profile.customer;
      this.invoice_doc = "";
    },
    new_invoice(data = {}) {
      const doc = this.get_invoice_doc();
      if (doc.name) {
        this.update_invoice(doc);
      } else {
        if (doc.items.length) {
          this.save_draft_invoice(doc);
        }
      }
      if (!data.name) {
        this.items = [];
        this.customer = this.pos_profile.customer;
        this.invoice_doc = "";
      } else {
        this.invoice_doc = data;
        this.items = data.items;
        this.customer = data.customer;
      }
    },
    save_draft_invoice() {
      const vm = this;
      const doc = this.get_invoice_doc();
      frappe.call({
        method: "posawesome.posawesome.api.posapp.save_draft_invoice",
        args: { data: doc },
        async: false,
        callback: function (r) {
          if (r.message) {
            vm.invoice_doc = r.message;
          }
        },
      });
      return this.invoice_doc;
    },
    get_invoice_doc() {
      let doc = {};
      if (this.invoice_doc.name) {
        doc = { ...this.invoice_doc };
      }
      doc.doctype = "Sales Invoice";
      doc.is_pos = 1;
      doc.company = doc.company || this.pos_profile.company;
      doc.pos_profile = doc.pos_profile || this.pos_profile.name;
      doc.currency = doc.currency || this.pos_profile.currency;
      doc.naming_series = doc.naming_series || this.pos_profile.naming_series;
      doc.customer = this.customer;
      doc.items = this.get_invoice_items();
      doc.total = this.subtotal;
      (doc.posa_pos_opening_shift = this.pos_opening_shift.name),
        (doc.payments = doc.payments = this.get_payments()),
        (doc.taxes = []);
      return doc;
    },
    get_invoice_items() {
      const items_list = [];
      this.items.forEach((item) => {
        items_list.push({
          item_code: item.item_code,
          qty: item.qty,
          rate: item.rate,
        });
      });
      return items_list;
    },
    get_payments() {
      const payments = [];
      this.pos_profile.payments.forEach((payment) => {
        payments.push({
          amount: 0,
          mode_of_payment: payment.mode_of_payment,
          default: payment.default,
          account: "",
        });
      });
      return payments;
    },
    update_invoice(doc) {
      const vm = this;
      frappe.call({
        method: "posawesome.posawesome.api.posapp.update_invoice",
        args: {
          data: doc,
        },
        async: false,
        callback: function (r) {
          if (r.message) {
            vm.invoice_doc = r.message;
          }
        },
      });
      return this.invoice_doc;
    },
    proces_invoice() {
      const doc = this.get_invoice_doc();
      if (doc.name) {
        return this.update_invoice(doc);
      } else {
        return this.save_draft_invoice(doc);
      }
    },
    show_payment() {
      if (!this.customer) {
        evntBus.$emit("show_mesage", {
          text: `There is no Customer !`,
          color: "error",
        });
        return;
      }
      if (!this.items.length) {
        evntBus.$emit("show_mesage", {
          text: `There is no Items !`,
          color: "error",
        });
        return;
      }
      evntBus.$emit("show_payment", "true");
      const invoice_doc = this.proces_invoice();
      invoice_doc.customer_info = this.customer_info;
      evntBus.$emit("send_invoice_doc_payment", invoice_doc);
    },
    get_draft_invoices() {
      const vm = this;
      frappe.call({
        method: "posawesome.posawesome.api.posapp.get_draft_invoices",
        args: {
          pos_opening_shift: this.pos_opening_shift.name,
        },
        async: false,
        callback: function (r) {
          if (r.message) {
            evntBus.$emit("open_drafts", r.message);
          }
        },
      });
    },
    close_payments() {
      evntBus.$emit("show_payment", "false");
    },
    update_items_details(items) {
      const vm = this;
      frappe.call({
        method: "posawesome.posawesome.api.posapp.get_items_details",
        args: {
          pos_profile: vm.pos_profile,
          items_data: items,
        },
        callback: function (r) {
          if (r.message) {
            items.forEach((item) => {
              const updated_item = r.message.find(
                (element) => element.item_code == item.item_code
              );
              item.actual_qty = updated_item.actual_qty;
              item.serial_no_data = updated_item.serial_no_data;
              item.batch_no_data = updated_item.batch_no_data;
              item.uoms = updated_item.uoms;
              item.rate = updated_item.rate;
              item.currency = updated_item.currency;
            });
          }
        },
      });
    },
    update_item_detail(item) {
      const vm = this;
      frappe.call({
        method: "posawesome.posawesome.api.posapp.get_item_detail",
        args: {
          data: {
            item_code: item.item_code,
            warehouse: this.pos_profile.warehouse,
            customer: this.customer,
            selling_price_list: this.pos_profile.selling_price_list,
            price_list_currency: this.pos_profile.currency,
            doctype: "Sales Invoice",
            name: "New Sales Invoice 1",
            company: this.pos_profile.company,
            conversion_rate: 1,
            rate: item.rate,
          },
        },
        callback: function (r) {
          if (r.message) {
            // items.forEach((item) => {
            //   const updated_item = r.message.find(
            //     (element) => element.item_code == item.item_code
            //   );
            //   item.actual_qty = updated_item.actual_qty;
            //   item.serial_no_data = updated_item.serial_no_data;
            //   item.batch_no_data = updated_item.batch_no_data;
            //   item.uoms = updated_item.uoms;
            //   item.rate = updated_item.rate;
            //   item.currency = updated_item.currency;
            // });
            console.log(r.message);
          }
        },
      });
    },
    fetch_customer_details() {
      const vm = this;
      if (this.customer) {
        return new Promise((resolve) => {
          frappe.db
            .get_value("Customer", vm.customer, [
              "email_id",
              "mobile_no",
              "image",
              "loyalty_program",
            ])
            .then(({ message }) => {
              const { loyalty_program } = message;
              if (loyalty_program) {
                frappe.call({
                  method:
                    "erpnext.accounts.doctype.loyalty_program.loyalty_program.get_loyalty_program_details_with_points",
                  args: {
                    customer: vm.customer,
                    loyalty_program,
                    silent: true,
                  },
                  callback: (r) => {
                    const { loyalty_points, conversion_factor } = r.message;
                    if (!r.exc) {
                      vm.customer_info = {
                        ...message,
                        customer: vm.customer,
                        loyalty_points,
                        conversion_factor,
                      };
                      resolve();
                    }
                  },
                });
              } else {
                vm.customer_info = { ...message, customer: vm.customer };
                resolve();
              }
            });
        });
      } else {
        return new Promise((resolve) => {
          vm.customer_info = {};
          resolve();
        });
      }
    },
    calc_prices(item, value, $event) {
      if (event.target.id === "rate") {
        item.discount_percentage = 0;
        if (value < item.price_list_rate) {
          item.discount_amount = flt(item.price_list_rate) - flt(value);
        } else if (value < 0) {
          item.rate = item.price_list_rate;
          item.discount_amount = 0;
        }
        else if (value > item.price_list_rate) {
          item.discount_amount = 0;
        }
      } else if (event.target.id === "discount_amount") {
        if (value < 0) {
          item.discount_amount = 0;
          item.discount_percentage = 0;
        } else {
          item.rate = flt(item.price_list_rate) - flt(value);
          item.discount_percentage = 0;
        }
      } else if (event.target.id === "discount_percentage") {
        if (value < 0) {
          item.discount_amount = 0;
          item.discount_percentage = 0;
        } else {
          item.rate =
            flt(item.price_list_rate) -
            (flt(item.price_list_rate) * flt(value)) / 100;
          item.discount_amount = flt(item.price_list_rate) - flt(item.rate);
        }
      }
    },
  },
  created() {
    this.$nextTick(function () {});
    evntBus.$on("register_pos_profile", (data) => {
      this.pos_profile = data.pos_profile;
      this.customer = data.pos_profile.customer;
      this.pos_opening_shift = data.pos_opening_shift;
    });
    evntBus.$on("add_item", (item) => {
      // this.expanded = []
      this.add_item(item);
    });
    evntBus.$on("update_customer", (customer) => {
      this.customer = customer;
    });
    evntBus.$on("new_invoice", () => {
      this.invoice_doc = "";
      this.cancel_invoice();
    });
    evntBus.$on("load_invoice", (data) => {
      this.new_invoice(data);
    });
  },
  mounted: function () {},
  watch: {
    customer() {
      this.close_payments();
      evntBus.$emit("set_customer", this.customer);
      this.fetch_customer_details();
      // this.get_loyalty();
    },
    expanded(data_value) {
      this.update_items_details(data_value);
    },
  },
};
